___CSS_LOADER_EXPORT___.push([module.id, `.dtable-ui-link-editor-popover .link-options-search {
  padding: 10px 10px 0 10px;
}

.dtable-ui-link-editor-popover .link-options-search input {
  height: 28px;
}

.dtable-ui-link-editor-popover .link-options-container {
  min-height: 160px;
  max-height: 200px;
  margin: 10px 0;
  padding: 0 10px;
  overflow: auto;
}

/* option item */
.dtable-ui-link-editor-popover .link-option-item {
  display: flex;
  height: 30px;
  padding-left: 12px;
  font-size: 14px;
  line-height: 30px;
  cursor: pointer;
}

.dtable-ui-link-editor-popover .link-option-item:hover {
  background-color: #f5f5f5;
}

.dtable-ui-link-editor-popover .option-info {
  flex: 1;
  display: flex;
  align-items: center;
}

.dtable-ui-link-editor-popover .option-info .option-name {
  max-width: 250px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dtable-ui-link-editor-popover .option-checked {
  width: 20px;
  text-align: center;
}

.dtable-ui-link-editor-popover .option-checked .dtable-font {
  font-size: 12px;
  color: #798d99;
}

.dtable-ui-link-editor-popover .select-options-add {
  display: flex;
  align-items: center;
  padding: 0 10px;
  height: 30px;
  border-top: 1px solid #ccc;
}
